import React from 'react'

class OperationBox extends React.Component {
  constructor(props) {
    super(props)
  }
  handleAbortButtonClick = (e) => {
    if (!this.props.abortable) {
      alert('')
    }
  }
  handleConfirmButtonClick = (e) => {
    if (!this.props.confirmable) {
      alert('')
    }
  }
  render() {
    return(
      <div>
        <a href={this.props.abortable ? Routes.abort_cleaner_path() : 'javascript:void(0);'} data-method='post' onClick={this.handleAbortButtonClick} className='button radius alert'></a>
        <a href={this.props.confirmable ? Routes.confirm_cleaner_path() : 'javascript:void(0);'} data-method='post' onClick={this.handleConfirmButtonClick} className='button radius'></a>
      </div>
    )
  }
}

export default OperationBox



