import { UPDATE_CLEANER_STATUS } from '../actions/rootAction'

const initialState = {
  progress_bar: {
    state_message: '...',
    value:         0,
  },
  operation: {
    abortable:   true,
    confirmable: false,
  },
  modal: {
    server: {
      busyness:             '',
      busyness_message:     '',
      processing_job_count: 0,
    },
    job: {
      collect_count: 0,
      destroy_count: 0,
    },
    job_params: {
      user_name:        '',
      from:             '',
      to:               '',
      protect_reply:    '',
      protect_favorite: '',
      created_at:       '',
    },
  },
}

export default function rootReducer(state = initialState, action) {
  switch (action.type) {
    case UPDATE_CLEANER_STATUS:
      return Object.assign({}, state, action.cleanerStatus)
    default:
      return state
  }
}



